html, body {
  min-height: 100%;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background: #101010;
  overflow-x: hidden;
  overflow-y: auto;
  
  overflow: hidden;
}

body {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  /* background-image: radial-gradient(farthest-side at 0 0,rgba(255,26,198,.3),rgba(255,26,198,0)),radial-gradient(farthest-side at 100% 0,rgba(60,221,221,.4),rgba(60,221,221,0)),radial-gradient(farthest-side at 50% 0,rgba(255,26,198,.1),rgba(255,26,198,0)); */
  cursor: -webkit-grab;
  cursor: grab;
}


@font-face {
  font-family: 'Apex Regular';
  font-style: normal;
  font-weight: normal;
  src: local('Apex Regular'), url('/assets/fonts/ApexMk2-LightCondensed.otf') format('opentype');
}


/* -------------------------------------------------  */

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
.debug {
  position: fixed;
  top: 10px;
  left: 10px;
  z-index: 1100;
  opacity: 0.1;
  -webkit-transition: opacity 0.2s ease-in-out;
  transition: opacity 0.2s ease-in-out;
}

.debug:hover {
  opacity: 1;
}

main {
  color: #fff;
  z-index: 500;
  position: absolute;
  pointer-events: none;
}

section {
  display: flex;
  width: 100vw;
  min-height: 120vh;
  justify-content: center;
  pointer-events: none;
  outline: 1px dashed fuchsia;
}

section .page {
  width: 80%;
  max-width: 1000px;
}


section:first-child {
}

h1 {
  display: inline-block;;
  font-family: Apex Regular, Helvetica, sans-serif;
  font-size: 100px;
  /* font-weight: 700; */
  text-transform: uppercase;
  margin-bottom: 24px;
  padding: 0.4rem;
  /* background-image: linear-gradient(120deg, #b8cbb8 0%, #b8cbb8 0%, #b465da 0%, #cf6cc9 33%, #ee609c 66%, #ee609c 100%); */
  /* background-image: linear-gradient(to right, #f78ca0 0%, #f9748f 19%, #fd868c 60%, #fe9a8b 100%); */
}


section h1 {
  -webkit-transform: translate(-500px, 0);
          transform: translate(-500px, 0);
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  -webkit-transition-delay: 0s;
          transition-delay: 0s;
}

section.active h1 {
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
}

h1 span::before {
  content: '';
  background-image: -webkit-linear-gradient(110deg, #b721ff 0%, #21d4fd 100%);
  background-image: linear-gradient(-20deg, #b721ff 0%, #21d4fd 100%);
  width: 80%;
  height: 90%;
  display: block;
  position: absolute;
  -webkit-transform: translate(-30px, -45px);
          transform: translate(-30px, -45px);  
  z-index: -1;  
  -webkit-transition: all 1s ease-in-out;  
  transition: all 1s ease-in-out;
}

h1 span:hover::before {
  background-image: -webkit-linear-gradient(330deg, #b8cbb8 0%, #b8cbb8 0%, #b465da 0%, #cf6cc9 33%, #ee609c 66%, #ee609c 100%);
  background-image: linear-gradient(120deg, #b8cbb8 0%, #b8cbb8 0%, #b465da 0%, #cf6cc9 33%, #ee609c 66%, #ee609c 100%);
  -webkit-transform: translate(-40px, -45px);
          transform: translate(-40px, -45px);  
  opacity: 0.5;
}

h1 span {
  position: relative;
}

section p {
  font-family: Arial, Helvetica, sans-serif;
  color: #fff;
  font-size: 20px;
  line-height: 24px;
  background-color: rgba(0, 0, 0, 0.6);
  display: inline-block;
  max-width: 80%;
  margin-bottom: 24px;
}

section p span {
  position: relative;
}
