.cursor {
  position: absolute;
  display: block;
  width: 25px;
  height: 25px;
  /* background: rgba(255, 255, 255, 0.2); */
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  transition: all 0.2s ease-out;
  z-index: 1000;
  pointer-events: none;
}

.cursor.active {
  /* background: white; */
  border: 1px solid rgba(255, 255, 255, 1);
  /* background: rgba(255, 255, 255, 0.5); */
  cursor: pointer;
  width: 50px;
  height: 50px;
}

.wrapper {
  position: fixed;
  z-index: 1000;
  pointer-events: none;
  height: 100%;
  width: 100%;
}