.debug {
  position: fixed;
  top: 10px;
  left: 10px;
  z-index: 1100;
  opacity: 0.1;
  transition: opacity 0.2s ease-in-out;
}

.debug:hover {
  opacity: 1;
}

main {
  color: #fff;
  z-index: 500;
  position: absolute;
  pointer-events: none;
}

section {
  display: flex;
  width: 100vw;
  min-height: 120vh;
  justify-content: center;
  pointer-events: none;
  outline: 1px dashed fuchsia;
}

section .page {
  width: 80%;
  max-width: 1000px;
}


section:first-child {
}

h1 {
  display: inline-block;;
  font-family: Apex Regular, Helvetica, sans-serif;
  font-size: 100px;
  /* font-weight: 700; */
  text-transform: uppercase;
  margin-bottom: 24px;
  padding: 0.4rem;
  /* background-image: linear-gradient(120deg, #b8cbb8 0%, #b8cbb8 0%, #b465da 0%, #cf6cc9 33%, #ee609c 66%, #ee609c 100%); */
  /* background-image: linear-gradient(to right, #f78ca0 0%, #f9748f 19%, #fd868c 60%, #fe9a8b 100%); */
}


section h1 {
  transform: translate(-500px, 0);
  transition: all 0.3s ease-in-out;
  transition-delay: 0s;
}

section.active h1 {
  transform: translate(0, 0);
}

h1 span::before {
  content: '';
  background-image: linear-gradient(-20deg, #b721ff 0%, #21d4fd 100%);
  width: 80%;
  height: 90%;
  display: block;
  position: absolute;
  transform: translate(-30px, -45px);  
  z-index: -1;  
  transition: all 1s ease-in-out;
}

h1 span:hover::before {
  background-image: linear-gradient(120deg, #b8cbb8 0%, #b8cbb8 0%, #b465da 0%, #cf6cc9 33%, #ee609c 66%, #ee609c 100%);
  transform: translate(-40px, -45px);  
  opacity: 0.5;
}

h1 span {
  position: relative;
}

section p {
  font-family: Arial, Helvetica, sans-serif;
  color: #fff;
  font-size: 20px;
  line-height: 24px;
  background-color: rgba(0, 0, 0, 0.6);
  display: inline-block;
  max-width: 80%;
  margin-bottom: 24px;
}

section p span {
  position: relative;
}